import { FILL_AZURE_LIGHT, FILL_YELLOW_LIGHT, FILL_GRAY_LIGHT, BORDER_AZURE, FONT_YELLOW, BORDER_LIGHT_GRAY, FONT_GRAY, FONT_BLACK } from '../templates/colors';

import {
  getCurrenciesFromTableData,
  getLeaseProjectionDuration,
  getLeaseProjectionData,
} from '../lib/projection';
import { getAdjustmentData } from "../lib/adjustment";

const COLUMN_COUNT = 4;

export default class Calculation extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
    };
  }

  /**
   * Format the border of range
   * @param {object} range excel object to indicate the range to set formate
   * @param {string} color border color
   * @param {string} weight border weight
   */
  _formatRangeBorder = (range, color, weight) => {
    range.format.borders.getItem('EdgeBottom').color = color;
    range.format.borders.getItem('EdgeBottom').weight = weight;

    range.format.borders.getItem('EdgeLeft').color = color;
    range.format.borders.getItem('EdgeLeft').weight = weight;

    range.format.borders.getItem('EdgeRight').color = color;
    range.format.borders.getItem('EdgeRight').weight = weight;

    range.format.borders.getItem('EdgeTop').color = color;
    range.format.borders.getItem('EdgeTop').weight = weight;
  }

  /**
   * Arrange Raw Data for Processing
   * @param {array} header array of header titles
   * @param {array} body array of data body
   * @param {array} format array of formats
   * @returns {array} array of arranged data
   */
  _arrangeTableData = (header, body, format) => {
    let data = [];
    for (var i in body) {
      let item = {};
      for (var j in header) {
        item[header[j].toLowerCase()] = body[i][j]
        if (format[j] == 'dd.mm.yy') {
          item[header[j].toLowerCase()] = excelDateToJSDate(body[i][j]);
        }
      }
      data.push(item);
    }
    return data;
  }

  _getReportRange = async () => {
    return new Promise((resolve, reject) => {
      Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_REPORT_RANGE_TABLE[0];
        let startRow = START_INDEX_REPORT_RANGE_TABLE[1];

        let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

        let index = COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + (startRow - 0 + 1) + ':' + COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + (startRow - 0 + 2);
        let range = sheet.getRange(index);
        let tmp = range.load('values');
        return context.sync()
          .then(() => {
            let values = tmp.values;
            let start = excelDateToJSDate(values[0][0]);
            let end = excelDateToJSDate(values[1][0]);

            let months = [];
            let startYear = start.getFullYear();
            let startMonth = start.getMonth() + 1;
            let endYear = end.getFullYear();
            let endMonth = end.getMonth() + 1;

            for (var i = startMonth; i <= 12; i++) {
              months.push([startYear, i])
            }
            for (var i = startYear + 1; i < endYear; i++) {
              for (var j = 1; j <= 12; j++) {
                months.push([i, j])
              }
            }
            for (var i = 1; i <= endMonth; i++) {
              months.push([endYear, i]);
            }
            resolve(months);
          })
      })
    })
  }

  _getDefaultCurrency = async () => {
    const _self = this;
    return new Promise((resolve, reject) => {
      Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_DEFAULT_CURRENCY[0];
        let startRow = START_INDEX_DEFAULT_CURRENCY[1];

        let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

        let index = COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + startRow;
        let range = sheet.getRange(index);
        let tmp = range.load('values');
        return context.sync()
          .then(() => {
            let values = tmp.values;
            let defaultCurrency = values[0][0];
            _self.setState({ defaultCurrency });
            resolve();
          })
      })
    })
  }

  _getIndexationData = async () => {
    const _self = this;
    return new Promise((resolve, reject) => {
      Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_INDEXATION_TABLE[0];
        let startRow = START_INDEX_INDEXATION_TABLE[1];

        let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

        let index = COLUMN_INDEXES[startColumnIndex] + (startRow - 0 + 1) + ':' + COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + (startRow - 0 + 2);
        let range = sheet.getRange(index);
        let tmp = range.load('values');
        return context.sync()
          .then(() => {
            let values = tmp.values;
            let title = values[0];
            values = values[1]
            let indexationData = {};
            for (var i in title) {
              indexationData[title[i]] = values[i]
            }
            console.log(indexationData)
            _self.setState({ indexationData });
            resolve();
          })
      })
    })
  }

  /**
   * Render Currency Conversion Table
   * @param {array} tableData arranged data
   */
  _renderCurrencyConversionTable = async (tableData) => {
    const _self = this;
    console.log('render currency conversion table')
    return new Promise(async (resolve, reject) => {
      let currencies = getCurrenciesFromTableData(_self.state.defaultCurrency, tableData);

      this.setState({
        currencyUnitsCount: currencies.length
      })

      let exchangeData = await getExchangeRates(_self.state.defaultCurrency, currencies);
      this.setState({ exchangeData })

      Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_CURRENCY_CONVERSION[0];
        let startRow = START_INDEX_CURRENCY_CONVERSION[1];

        let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

        let index = startColumn + startRow;
        var range = sheet.getRange(index);
        range.values = [["Currency Conversion"]];

        range = sheet.getRange(index + ':' + COLUMN_INDEXES[startColumnIndex + COLUMN_COUNT] + startRow);
        range.merge();

        _self._formatRangeBorder(range, BORDER_AZURE, 'Medium');

        var now = new Date();
        var y = now.getFullYear();
        var m = now.getMonth() + 1;
        var d = now.getDate();

        let currencyUnits = exchangeData[exchangeData.length - 1].rates;

        let offset = 0;
        for (var currency in currencyUnits) {
          let row = (startRow - 0 + 2 + offset);
          index = startColumn + row;
          range = sheet.getRange(index);
          range.values = [[currency]];
          range.format.fill.color = 'lightgray';

          for (var j = 0; j < COLUMN_COUNT; j++) {
            let row = (startRow - 0 + 1);
            index = COLUMN_INDEXES[COLUMN_INDEXES.indexOf(startColumn) + j + 1] + row;
            range = sheet.getRange(index);
            let title = "";
            if (j == 0) {
              title = "Present";
            } else {
              title = (d < 10 ? '0' + d : d) + '.' + (m < 10 ? '0' + m : m) + '.' + (y + j - 2000);
              range.format.fill.color = FILL_YELLOW_LIGHT;
            }
            range.values = [[title]];

            row = (startRow - 0 + 2 + offset);
            index = COLUMN_INDEXES[COLUMN_INDEXES.indexOf(startColumn) + j + 1] + row;
            range = sheet.getRange(index);
            range.values = [[currencyUnits[currency]]]
            range.format.fill.color = FILL_YELLOW_LIGHT;
          }
          offset++;
        }
        index = startColumn + (startRow - 0 + 1) + ':' + COLUMN_INDEXES[COLUMN_INDEXES.indexOf(startColumn) + COLUMN_COUNT] + (startRow - 0 + offset + 1);
        range = sheet.getRange(index);
        _self._formatRangeBorder(range, BORDER_AZURE, 'Medium');
        resolve();
        return context.sync();
      });
    })
  }

  /**
   * Render Lease Projection Table
   * @param {array} data json array data
   * @param {array} duration array of months
   */
  _renderLeaseProjectionTable = (data, duration) => {
    console.log('render lease projection table')

    Excel.run(async context => {
      var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
      let startColumn = START_INDEX_REPORT_TABLE[0];
      let startRow = START_INDEX_REPORT_TABLE[1];

      let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

      let index = startColumn + startRow + ':' + startColumn + (startRow - 0 + 1);
      var range = sheet.getRange(index);
      range.values = [["Start"], ["End"]];
      range.format.fill.color = FILL_AZURE_LIGHT;

      // render tenant area
      let value = []
      for (var i in data) {
        let tmp = [];
        tmp.push(data[i][HEADER_TENANT_NAME.toLowerCase()]);
        tmp.push(data[i][HEADER_TYPE_OF_AREA.toLowerCase()]);
        value.push(tmp);
      }
      index = startColumn + (startRow - 0 + 2) + ':' + COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 1 + data.length)
      range = sheet.getRange(index);
      range.values = value;
      range.format.fill.color = FILL_AZURE_LIGHT;
      // render tenant area

      // render start/end month duration
      let startTitleArray = [];
      let endTitleArray = [];
      for (var i = 0; i < duration.length; i++) {

        let endDate = getEndDateOfMonth(duration[i][0], duration[i][1] - 1);
        startTitleArray.push('01.' + (duration[i][1] < 10 ? ('0' + duration[i][1]) : duration[i][1]) + '.' + (duration[i][0] - 2000));
        endTitleArray.push((endDate < 10 ? ('0' + endDate) : endDate) + '.' + (duration[i][1] < 10 ? ('0' + duration[i][1]) : duration[i][1]) + '.' + (duration[i][0] - 2000));
      }
      value = [startTitleArray, endTitleArray];
      index = COLUMN_INDEXES[startColumnIndex + 2] + startRow + ':' + COLUMN_INDEXES[startColumnIndex + 1 + duration.length] + (startRow - 0 + 1);
      range = sheet.getRange(index);
      range.values = value;
      range.format.fill.color = "lightgray";
      // end render start/end month duration

      // render data
      for (var i = 0; i < data.length; i++) {
        // render data by months
        let row = startRow + 2 + i;
        let projectionList = data[i]['projection-list'];

        let value = [];
        let columnOffset = 0;

        for (var key in projectionList) {
          let item = projectionList[key];

          let column = COLUMN_INDEXES[startColumnIndex + 2 + columnOffset];
          let index = column + row;

          switch (item.type) {
            case 'break':
              value.push("VACANT");
              range = sheet.getRange(index);
              range.format.font.color = "gray";
              range.format.font.italic = true;
              break;
            case 'rental':
              value.push(item.value);
              range = sheet.getRange(index);
              range.format.font.color = "green";
              range.format.fill.color = "lightgreen";
              range.format.font.italic = false;
              break;
            case 'srent':
              value.push(item.value);
              range = sheet.getRange(index);
              range.format.font.color = "red";
              range.format.fill.color = "lightpink";
              range.format.font.italic = false;
              break;
            case 'indexation':
              value.push(item.value);
              range = sheet.getRange(index);
              range.format.font.color = FONT_YELLOW;
              range.format.fill.color = FILL_GRAY_LIGHT;
              range.format.font.italic = false;
              break;
            case 'erv':
              value.push(item.value);
              range = sheet.getRange(index);
              range.format.font.color = "black";
              range.format.font.italic = false;
              range.format.fill.color = FILL_AZURE_LIGHT;
              break;
            case 'erv-indexation':
              value.push(item.value);
              range = sheet.getRange(index);
              range.format.font.color = FONT_YELLOW;
              range.format.fill.color = FILL_GRAY_LIGHT;
              range.format.font.italic = false;
              break;
            case 'erv-break':
              value.push("VACANT");
              range = sheet.getRange(index);
              range.format.font.color = "gray";
              range.format.font.italic = true;
              range.format.fill.color = FILL_AZURE_LIGHT;
              break;
            default:
              value.push(item.value);
              break;
          }
          columnOffset++;
        }
        index = COLUMN_INDEXES[startColumnIndex + 2] + row + ':' + COLUMN_INDEXES[startColumnIndex + 1 + duration.length] + row;
        range = sheet.getRange(index);
        range.values = [value];
      }

      console.log('complete render lease projection')

      // end render data
      return context.sync();
    });
  }

  /**
   * Render KPI Table and Chart
   * @param {array} data data with projection list
   * @param {array} occupancies array of occupancies
   * @param {array} duration array of months
   */
  _renderKPI_Table = (data, occupancies, duration) => {
    const _self = this;
    console.log('render kpi table')
    return new Promise(resolve => {
      Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_KPI_TABLE[0];
        let startRow = START_INDEX_KPI_TABLE[1];

        let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

        let index = startColumn + startRow;
        var range = sheet.getRange(index);
        range.values = [["Yearly Key Performance Indicators (Placeholder for now)"]];

        range = sheet.getRange(index + ':' + COLUMN_INDEXES[startColumnIndex + 8] + startRow);
        range.merge();

        _self._formatRangeBorder(range, BORDER_AZURE, 'Medium');

        let value = [
          ["Year"],
          ["Total Income"],
          ["Total ERV"],
          ["Avg. Occupancy"]
        ]
        index = startColumn + (startRow - 0 + 1) + ':' + startColumn + (startRow - 0 + 4);
        range = sheet.getRange(index);
        range.values = value;
        range.format.fill.color = FILL_AZURE_LIGHT;
        _self._formatRangeBorder(range, BORDER_LIGHT_GRAY, 'Thin')
        // range.format.autofitColumns();

        let startYear = duration[0][0];
        let endYear = duration[duration.length - 1][0];

        let yearTitles = [];
        let totalIncomeValues = [];
        let totalErvValues = [];
        let AvgOccupancyValues = [];
        for (var year = startYear; year <= endYear; year++) {
          yearTitles.push(year);

          // get sum
          let totalIncome = 0;
          let totalERV = 0;

          let occupancy = 0;
          let occupancyCount = 0;
          for (var key in occupancies) {
            if (key.includes(year + '-')) {
              occupancy += occupancies[key];
              occupancyCount++;
            }
          }

          for (var j in data) {
            let elem = data[j];
            let projectionList = elem['projection-list']

            let rentIncomeMonthly = elem[HEADER_RENT_INCOME_MONTHLY.toLowerCase()];
            let leaseStart = elem[HEADER_LEASE_START.toLowerCase()];
            let leaseEnd = elem[HEADER_LEASE_EXPIRY.toLowerCase()];
            let breakOptionDate = elem[HEADER_BREAK_OPTION_DATE.toLowerCase()];
            let ERVForNewLease_Month = elem[HEADER_ERV_FOR_NEW_LEASE_MONTH.toLowerCase()];

            let estimatedIndefinitePeriodEnd = elem[HEADER_ESTIMATED_INDEFINITE_PERIOD_END.toLowerCase()];
            if (leaseEnd == null) leaseEnd = estimatedIndefinitePeriodEnd;

            let exchangeRate = 1;
            // get total income
            for (var monthKey in projectionList) {
              let y = parseInt(monthKey.split('-')[0]);
              if (year == y) {
                if (typeof projectionList[monthKey].value == 'number') totalIncome += projectionList[monthKey].value;
                exchangeRate = projectionList[monthKey].exchangeRate;
              }
            }
            // end get total income

            // get total erv
            // console.log(i, j, leaseEnd.getFullYear())
            if (leaseEnd.getFullYear() <= year) {
              totalERV += ERVForNewLease_Month / exchangeRate;
            } else {
              totalERV += rentIncomeMonthly / exchangeRate;
            }
            totalERV = parseInt(totalERV);
            // end get total income
          }
          totalIncomeValues.push(totalIncome);
          totalErvValues.push(totalERV);
          AvgOccupancyValues.push(occupancy / occupancyCount);
        }
        // // end get sum
        index = COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 1) + ':' + COLUMN_INDEXES[startColumnIndex + endYear - startYear + 1] + (startRow - 0 + 4);
        range = sheet.getRange(index);
        range.values = [
          yearTitles,
          totalIncomeValues,
          totalErvValues,
          AvgOccupancyValues
        ];

        index = COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 1) + ':' + COLUMN_INDEXES[startColumnIndex + endYear - startYear + 1] + (startRow - 0 + 1);
        range = sheet.getRange(index);
        range.format.fill.color = FILL_AZURE_LIGHT;
        _self._formatRangeBorder(range, BORDER_LIGHT_GRAY, 'Thin')


        index = COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 4) + ':' + COLUMN_INDEXES[startColumnIndex + endYear - startYear + 1] + (startRow - 0 + 4);
        range = sheet.getRange(index);
        range.numberFormat = [['0%;;"0%"']];
        resolve();
        return context.sync();
      });
    })
  }

  /**
   * Render charts for incoming and occupancies
   * @param {array} duration array of months
   */
  _renderChart = async (duration) => {
    console.log('render chart')
    let startYear = duration[0][0];
    let endYear = duration[duration.length - 1][0];

    Excel.run(async context => {
      var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
      let startColumn = START_INDEX_KPI_TABLE[0];
      let startRow = START_INDEX_KPI_TABLE[1];

      let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

      let index = COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 6);
      let startCellForIncomeChart = sheet.getRange(index);

      index = COLUMN_INDEXES[startColumnIndex + 6] + (startRow - 0 + 17);
      let endCellForIncomeChart = sheet.getRange(index);

      index = COLUMN_INDEXES[startColumnIndex + 8] + (startRow - 0 + 6);
      let startCellForOccupancyChart = sheet.getRange(index);

      index = COLUMN_INDEXES[startColumnIndex + 13] + (startRow - 0 + 17);
      let endCellForOccupancyChart = sheet.getRange(index);

      index = COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 1) + ':' + COLUMN_INDEXES[startColumnIndex + endYear - startYear + 1] + (startRow - 0 + 1)
      var categoryRange = sheet.getRange(index);

      index = COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 2) + ':' + COLUMN_INDEXES[startColumnIndex + endYear - startYear + 1] + (startRow - 0 + 2)
      var dataRange = sheet.getRange(index);

      // add total incoming chart
      let chartIncome = sheet.charts.add("LineMarkers", dataRange, "Rows");
      chartIncome.name = "chart-total-income";
      chartIncome.title.text = "Total Income";
      chartIncome.axes.categoryAxis.setCategoryNames(categoryRange);
      chartIncome.setPosition(startCellForIncomeChart, endCellForIncomeChart);
      // end add total incoming chart

      // add avg occupancy chart
      index = COLUMN_INDEXES[startColumnIndex + 1] + (startRow - 0 + 4) + ':' + COLUMN_INDEXES[startColumnIndex + endYear - startYear + 1] + (startRow - 0 + 4)
      dataRange = sheet.getRange(index);
      var chartOccupancy = sheet.charts.add("LineMarkers", dataRange, "Rows");
      chartOccupancy.title.text = "Avg. Occupancy";
      chartOccupancy.axes.categoryAxis.setCategoryNames(categoryRange)
      chartOccupancy.setPosition(startCellForOccupancyChart, endCellForOccupancyChart);

      return context.sync()
    })
  }

  /**
   * Render GLA Table
   * @param {array} data data with projection list
   * @param {array} duration array of months
   */
  _renderGLA_Table = async (data, duration) => {
    console.log('render gla table')
    const _self = this;
    return new Promise((resolve, reject) => {
      Excel.run(async context => {
        var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
        let startColumn = START_INDEX_GLA_TABLE[0];
        let startRow = START_INDEX_GLA_TABLE[1];

        let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);

        let index = startColumn + startRow;
        var range = sheet.getRange(index);
        range.values = [["Net Rental Income Monthly (Placeholder for now)"]];

        range = sheet.getRange(index + ':' + COLUMN_INDEXES[startColumnIndex + 8] + startRow);
        range.merge();

        _self._formatRangeBorder(range, BORDER_AZURE, 'Medium');

        let value = [
          ["Occupancy GLA"],
          ["Vacant GLA"],
          ["Vacant ERV GLA"]
        ]
        index = startColumn + (startRow - 0 + 1) + ':' + startColumn + (startRow - 0 + 3);
        range = sheet.getRange(index);
        range.values = value;

        let occupancies = {};

        for (var i = 0; i < duration.length; i++) {
          let month = duration[i][0] + '-' + duration[i][1];

          let vacantGLA = 0;
          let totalVacantGLA = 0;
          let vacantErvGla = 0;

          for (var j = 0; j < data.length; j++) {
            let projection = data[j];
            let projectionList = projection['projection-list'];
            for (var key in projectionList) {
              let elem = projectionList[key];
              if (month == key) {
                let exchangeRate = projectionList[key].exchangeRate;
                if (typeof elem.value != 'number') {
                  vacantGLA += projection[HEADER_GROSS_LETTABLE_AREA_SQM.toLowerCase()] / exchangeRate;
                  if (elem.type == 'break') {
                    vacantErvGla += projection[HEADER_RENT_INCOME_MONTHLY.toLowerCase()] / exchangeRate;
                  } else if (elem.type == 'erv-break') {
                    vacantErvGla += projection[HEADER_ERV_FOR_NEW_LEASE_MONTH.toLowerCase()] / exchangeRate;
                  }
                }
                totalVacantGLA += projection[HEADER_GROSS_LETTABLE_AREA_SQM.toLowerCase()] / exchangeRate;
              }
            }
          }

          let occupancy = (totalVacantGLA - vacantGLA) / totalVacantGLA;
          occupancies[month] = occupancy;

          index = COLUMN_INDEXES[startColumnIndex - 0 + 2 + i] + (startRow - 0 + 1);
          range = sheet.getRange(index);
          range.values = [[occupancy]];
          range.numberFormat = [['0%;;"0%"']];

          index = COLUMN_INDEXES[startColumnIndex - 0 + 2 + i] + (startRow - 0 + 2);
          range = sheet.getRange(index);
          range.values = [[parseInt(vacantGLA)]];

          index = COLUMN_INDEXES[startColumnIndex - 0 + 2 + i] + (startRow - 0 + 3);
          range = sheet.getRange(index);
          range.values = [[parseInt(vacantErvGla)]];
        }
        resolve(occupancies)
        return context.sync()
      });
    })
  }

  /**
  * Render Adjustment Table
  * @param {array} data data with projection list
  * @param {array} duration array of month
  */
  _renderAdjustmentTable = async (data, duration) => {
    const _self = this;
    console.log('asdf');
    try {
      await Excel.run(async context => {
        let renderTable = () => {
          console.log('render adjustment table');
          var sheet = context.workbook.worksheets.getItem(ADJUSTMENT_SHEET_NAME);
          let startColumn = START_INDEX_ADJUSTMENT_TABLE[0];
          let startColumnIndex = COLUMN_INDEXES.indexOf(startColumn);
          let startRow = START_INDEX_ADJUSTMENT_TABLE[1];

          let values = [
            ["Sum Rent discount adjustment"],
            ["Sum Adjustment agent fees"],
            ["Sum Adjustment legal fees"],
            ["Sum Fit out contribution"],
          ]

          let fieldsSecond = [
            ["GLA"],
            ["Rent monthly"],
            ["Total rent"],
            ["Total agent fees"],
            ["Total legal fees"],
            ["Total fit out contribution"],
          ];

          let fieldsThird = [
            ["Average rent"],
            ["Invoiced rent"],
            ["Rent discount adjustment"],
            ["Adjustment agent fees"],
            ["Adjustment legal fees"],
            ["Fit out contribution"],
          ];

          let index = COLUMN_INDEXES[startColumnIndex + 5] + startRow + ':' + COLUMN_INDEXES[startColumnIndex + 5] + (startRow - 0 + 3);
          var range = sheet.getRange(index);
          range.values = values;
          range.format.autofitColumns();

          let startMonthTitles = [];
          let endMonthTitles = [];
          let arrSumRentDiscountAdjustment = [];
          let arrSumAdjustmentAgentFees = [];
          let arrSumAdjustmentLegalFees = [];
          let arrSumFitOutContribution = [];
          let empty = [];

          // render months & sums
          for (var i = 0; i < duration.length; i++) {
            empty.push(null);
            let row = startRow + 5;
            let column = COLUMN_INDEXES[startColumnIndex + 6 + i];

            let endDate = getEndDateOfMonth(duration[i][0], duration[i][1] - 1);
            let startTitle = '01.' + (duration[i][1] < 10 ? ('0' + duration[i][1]) : duration[i][1]) + '.' + (duration[i][0] - 2000);
            let endTitle = (endDate < 10 ? ('0' + endDate) : endDate) + '.' + (duration[i][1] < 10 ? ('0' + duration[i][1]) : duration[i][1]) + '.' + (duration[i][0] - 2000);
            startMonthTitles.push(startTitle);
            endMonthTitles.push(endTitle);

            let endRow = startRow + 6 + 12 * data.length;
            let SumRentDiscountAdjustment = `=SUMIFS(${column}$${row}:${column}$${endRow},$${COLUMN_INDEXES[startColumnIndex + 5]}${row}:$${COLUMN_INDEXES[startColumnIndex + 5]}$${endRow},"Rent discount adjustment")`;
            arrSumRentDiscountAdjustment.push(SumRentDiscountAdjustment);

            let SumAdjustmentAgentFees = `=SUMIFS(${column}$${row}:${column}$${endRow},$${COLUMN_INDEXES[startColumnIndex + 5]}${row}:$${COLUMN_INDEXES[startColumnIndex + 5]}$${endRow},"Adjustment agent fees")`;
            arrSumAdjustmentAgentFees.push(SumAdjustmentAgentFees);

            let SumAdjustmentLegalFees = `=SUMIFS(${column}$${row}:${column}$${endRow},$${COLUMN_INDEXES[startColumnIndex + 5]}${row}:$${COLUMN_INDEXES[startColumnIndex + 5]}$${endRow},"Adjustment legal fees")`;
            arrSumAdjustmentLegalFees.push(SumAdjustmentLegalFees);

            let SumFitOutContribution = `=SUMIFS(${column}$${row}:${column}$${endRow},$${COLUMN_INDEXES[startColumnIndex + 5]}${row}:$${COLUMN_INDEXES[startColumnIndex + 5]}$${endRow},"Fit out contribution")`;
            arrSumFitOutContribution.push(SumFitOutContribution);
          }

          index = COLUMN_INDEXES[startColumnIndex + 6] + startRow + ':' + COLUMN_INDEXES[startColumnIndex + 6 + duration.length - 1] + (startRow - 0 + 6);
          range = sheet.getRange(index);
          range.values = [
            arrSumRentDiscountAdjustment,
            arrSumAdjustmentAgentFees,
            arrSumAdjustmentLegalFees,
            arrSumFitOutContribution,
            empty,
            startMonthTitles,
            endMonthTitles
          ]
          index = COLUMN_INDEXES[startColumnIndex + 6] + (startRow - 0 + 5) + ':' + COLUMN_INDEXES[startColumnIndex + 6 + duration.length - 1] + (startRow - 0 + 6);
          sheet.getRange(index).format.fill.color = "lightgray";
          // end render months & sums

          for (var i = 0; i < data.length; i++) {
            let row = startRow + 7 + 12 * i;

            let { adjustmentData, metaData } = getAdjustmentData(data[i], duration, row, startColumnIndex);
            // show tenant name
            index = startColumn + row;
            range = sheet.getRange(index);
            range.values = [[data[i][HEADER_TENANT_NAME.toLowerCase()]]]

            index += ':' + startColumn + (row - 0 + 5);
            range = sheet.getRange(index);
            range.merge();
            range.format.verticalAlignment = 'Center';
            range.format.fill.color = FILL_AZURE_LIGHT;
            // end shw tenant name

            // show ex tenant name
            index = startColumn + (row - 0 + 6);
            range = sheet.getRange(index);
            range.values = [["EX " + data[i][HEADER_TENANT_NAME.toLowerCase()] + " ERV"]]
            index += ':' + startColumn + (row - 0 + 11);
            range = sheet.getRange(index);
            range.merge();
            range.format.verticalAlignment = 'Center';
            range.format.fill.color = FILL_AZURE_LIGHT;
            // show ex tenant name


            console.log('meta data', metaData)

            // current lease labels
            index = COLUMN_INDEXES[startColumnIndex + 1] + row + ':' + COLUMN_INDEXES[startColumnIndex + 2] + (row - 0 + 1);
            range = sheet.getRange(index);
            let leaseStart = metaData.currentLease.leaseStart;
            let leaseEnd = metaData.currentLease.leaseEnd;

            range.values = [
              ["Lease Start", (leaseStart.getMonth() - 0 + 1) + '/' + leaseStart.getDate() + '/' + leaseStart.getFullYear()],
              ["Lease End", (leaseEnd.getMonth() - 0 + 1) + '/' + leaseEnd.getDate() + '/' + leaseEnd.getFullYear()]
            ]
            range.numberFormat = "dd.mm.yy";

            // rental discounts
            if (metaData.currentLease.rentalDiscounts.length > 0) {
              index = COLUMN_INDEXES[startColumnIndex + 1] + (row - 0 + 2);
              range = sheet.getRange(index);
              range.values = [["Rental Discount 1"]];
              let discounts = [];
              for (var i in metaData.currentLease.rentalDiscounts) {
                discounts.push("Rental Discount " + (i - 0 + 1));
              }
              range.dataValidation.rule = {
                list: {
                  inCellDropDown: true,
                  source: discounts.join(',')
                }
              };

              let startMonthFormula = '"VACANT"';
              let endMonthFormula = '"VACANT"';
              let discountFormula = '"VACANT"';
              for (var i in metaData.currentLease.rentalDiscounts) {
                let item = metaData.currentLease.rentalDiscounts[i];

                let start = item[1].getDate() + '/' + (item[1].getMonth() - 0 + 1) + '/' + item[1].getFullYear();
                let end = item[2].getDate() + '/' + (item[2].getMonth() - 0 + 1) + '/' + item[2].getFullYear();
                let discount = item[0];

                let key = "Rental Discount " + (i - 0 + 1);
                startMonthFormula = `IF(${COLUMN_INDEXES[startColumnIndex + 1] + (row - 0 + 2)}="${key}", "${start}", ${startMonthFormula})`
                endMonthFormula = `IF(${COLUMN_INDEXES[startColumnIndex + 1] + (row - 0 + 2)}="${key}", "${end}", ${endMonthFormula})`
                discountFormula = `IF(${COLUMN_INDEXES[startColumnIndex + 1] + (row - 0 + 2)}="${key}", ${discount}, ${discountFormula})`
              }
              startMonthFormula = '=' + startMonthFormula;
              endMonthFormula = '=' + endMonthFormula;
              discountFormula = '=' + discountFormula;

              index = COLUMN_INDEXES[startColumnIndex + 1] + (row - 0 + 3) + ':' + COLUMN_INDEXES[startColumnIndex + 2] + (row - 0 + 5);
              range = sheet.getRange(index);
              range.values = [
                [`=CONCATENATE(${COLUMN_INDEXES[startColumnIndex + 1] + (row - 0 + 2)}, " Start")`, startMonthFormula],
                [`=CONCATENATE(${COLUMN_INDEXES[startColumnIndex + 1] + (row - 0 + 2)}, " Expiry")`, endMonthFormula],
                [`=CONCATENATE(${COLUMN_INDEXES[startColumnIndex + 1] + (row - 0 + 2)}, " Monthly")`, discountFormula]
              ]
              range.numberFormat = [
                ["", "dd.mm.yy"],
                ["", "dd.mm.yy"],
                ["", ""]
              ];
              range.format.autofitColumns();
            }

            index = COLUMN_INDEXES[startColumnIndex + 3] + row + ':' + COLUMN_INDEXES[startColumnIndex + 3] + (row - 0 + 5);
            range = sheet.getRange(index);
            range.values = fieldsSecond;
            range.format.autofitColumns();

            index = COLUMN_INDEXES[startColumnIndex + 4] + row + ':' + COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 5);
            range = sheet.getRange(index);
            range.values = [
              [metaData.currentLease.GLA],
              [metaData.currentLease.rentIncomeMonthly],
              [metaData.currentLease.totalAverageRent],
              [metaData.currentLease.totalAgentFee],
              [metaData.currentLease.totalLegalFee],
              [metaData.currentLease.totalFitOutContribution],
            ]

            index = COLUMN_INDEXES[startColumnIndex + 5] + row + ':' + COLUMN_INDEXES[startColumnIndex + 5] + (row - 0 + 5);
            range = sheet.getRange(index);
            range.values = fieldsThird;
            _self._formatRangeBorder(range, BORDER_AZURE, "Thin");
            range.format.fill.color = FILL_AZURE_LIGHT;
            // end current lease labels

            // future lease labels
            index = COLUMN_INDEXES[startColumnIndex + 1] + (row - 0 + 6) + ':' + COLUMN_INDEXES[startColumnIndex + 2] + (row - 0 + 7);
            range = sheet.getRange(index);
            leaseStart = metaData.newLease.leaseStart;
            leaseEnd = metaData.newLease.leaseEnd;

            range.values = [
              ["Lease Start", (leaseStart.getMonth() - 0 + 1) + '/' + leaseStart.getDate() + '/' + leaseStart.getFullYear()],
              ["Lease End", (leaseEnd.getMonth() - 0 + 1) + '/' + leaseEnd.getDate() + '/' + leaseEnd.getFullYear()]
            ]
            range.numberFormat = "dd.mm.yy";

            index = COLUMN_INDEXES[startColumnIndex + 3] + (row - 0 + 6) + ':' + COLUMN_INDEXES[startColumnIndex + 3] + (row - 0 + 11);
            range = sheet.getRange(index);
            range.values = fieldsSecond;

            index = COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 6) + ':' + COLUMN_INDEXES[startColumnIndex + 4] + (row - 0 + 11);
            range = sheet.getRange(index);
            range.values = [
              [metaData.newLease.GLA],
              [metaData.newLease.rentIncomeMonthly],
              [metaData.newLease.totalAverageRent],
              [metaData.newLease.totalAgentFee],
              [metaData.newLease.totalLegalFee],
              [metaData.newLease.totalFitOutContribution],
            ]

            index = COLUMN_INDEXES[startColumnIndex + 5] + (row - 0 + 6) + ':' + COLUMN_INDEXES[startColumnIndex + 5] + (row - 0 + 11);
            range = sheet.getRange(index);
            range.values = fieldsThird;
            _self._formatRangeBorder(range, BORDER_AZURE, "Thin");
            range.format.fill.color = FILL_AZURE_LIGHT;
            // end future lease labels
            // show data

            index = COLUMN_INDEXES[startColumnIndex + 6] + (row) + ':' + COLUMN_INDEXES[startColumnIndex + 6 + duration.length - 1] + (row - 0 + 11);
            range = sheet.getRange(index);
            range.values = [
              adjustmentData.arrAverageRent,
              adjustmentData.arrInvoicedRent,
              adjustmentData.arrRentDiscountAdjustment,
              adjustmentData.arrAdjustmentAgentFees,
              adjustmentData.arrAdjustmentLegalFees,
              adjustmentData.arrFitOutContribution,
              adjustmentData.arrExAverageRent,
              adjustmentData.arrExInvoicedRent,
              adjustmentData.arrExRentDiscountAdjustment,
              adjustmentData.arrExAdjustmentAgentFees,
              adjustmentData.arrExAdjustmentLegalFees,
              adjustmentData.arrExFitOutContribution,
            ]
            // end show data

            // add border
            index = startColumn + (row - 0 + 5) + ':' + COLUMN_INDEXES[startColumnIndex - 0 + 5 + duration.length] + (row - 0 + 5);
            range = sheet.getRange(index);
            range.format.borders.getItem("EdgeBottom").color = BORDER_LIGHT_GRAY;
            range.format.borders.getItem("EdgeBottom").weight = "Medium";
            range.format.borders.getItem("EdgeBottom").style = "Dash";

            index = startColumn + (row - 0 + 11) + ':' + COLUMN_INDEXES[startColumnIndex - 0 + 5 + duration.length] + (row - 0 + 11);
            range = sheet.getRange(index);
            range.format.borders.getItem("EdgeBottom").color = BORDER_AZURE;
            range.format.borders.getItem("EdgeBottom").weight = "Medium";
            // end add border
          }
        }

        renderTable();
      });
      // _self.setState({ isCalculating: false });
    } catch (error) {
      // _self.setState({ isCalculating: false });
      console.error(error);
    }
  }

  _recalculate = async () => {
    const _self = this;
    this.setState({ isCalculating: true })
    try {
      await Excel.run(async context => {
        var sheets = context.workbook.worksheets;
        var currentSheets = sheets.load("items");

        var dataSheet = context.workbook.worksheets.getItem(DATA_SHEET_NAME);
        var dataTable = dataSheet.tables.getItem(DATA_TABLE_NAME);

        // Get data from the header row
        var dataHeaderRange = dataTable.getHeaderRowRange().load("values");
        // Get data from the table
        var dataBodyRange = dataTable.getDataBodyRange().load("values");
        var dataFormatRange = dataTable.getDataBodyRange().load("numberFormat");

        return context.sync()
          .then(async () => {

            let renderTables = async () => {
              let duration = await _self._getReportRange();
              await _self._getDefaultCurrency();
              await _self._renderCurrencyConversionTable(tableData);
              await _self._getIndexationData();

              let leaseProjectionData = getLeaseProjectionData(tableData, duration, _self.state.exchangeData, _self.state.indexationData);
              console.log(leaseProjectionData)
              _self._renderLeaseProjectionTable(leaseProjectionData, duration)

              let occupancies = await _self._renderGLA_Table(leaseProjectionData, duration);
              await _self._renderKPI_Table(leaseProjectionData, occupancies, duration);
              _self._renderChart(duration);
              _self._renderAdjustmentTable(leaseProjectionData, duration)

              _self.setState({ isCalculating: false });
            }

            var exist = false;
            let tableData = _self._arrangeTableData(dataHeaderRange.values[0], dataBodyRange.values, dataFormatRange.numberFormat[0]);
            currentSheets.items.forEach(elem => {
              if (elem.name == REPORT_SHEET_NAME) {
                exist = true;
              }
            })

            if (exist) {
              console.log('recalculate');
              renderTables();
            } else {
              console.log('create report');
              var sheet = sheets.add(REPORT_SHEET_NAME);
              sheet.load("name, position");
              return context.sync()
                .then(async () => {
                  var sheet = context.workbook.worksheets.getItem(REPORT_SHEET_NAME);
                  sheet.activate();
                  sheet.load("name");
                  renderTables();
                });
            }
            return context.sync();
          });
      });
    } catch (error) {
      console.error(error);
    }
  }

  _showSourceTable = async () => {
    try {
      await Excel.run(async context => {
        var sheets = context.workbook.worksheets;
        var currentSheets = sheets.load("items");

        return context.sync()
          .then(async () => {

            var exist = false;
            currentSheets.items.forEach(elem => {
              if (elem.name == DATA_SHEET_NAME) {
                exist = true;
              }
            })

            if (exist) {
              console.log('show data sheet');
              var sheet = sheets.getItem(DATA_SHEET_NAME);
              sheet.activate()
            }
            return context.sync();
          });
      });
    } catch (error) {
      console.error(error);
    }
  }

  render() {
    const { isOfficeInitialized } = this.props;

    if (!isOfficeInitialized) {
      return (
        <div></div>
      );
    }

    return (
      <div className="section">
        <p className="section-title">Report</p>
        <div className="button-container">
          <div className="button" onClick={this._recalculate}>
            <FontAwesomeIcon className="icon" icon={faRedo} />
            <span>Recalculate</span>
          </div>
          <div className="button" onClick={this._showSourceTable}>
            <FontAwesomeIcon className="icon" icon={faTable} />
            <span>Source Sheet</span>
          </div>
        </div>
      </div>
    );
  }
}
